#sidebar-wrapper {
    min-width: 1440px;
    width: 100%;
    min-height: 2048px;
    height: 100%;
    display: flex;
    align-items: start;
    justify-content: start;
}

.sidebar-container {
    width: 500px;
    min-height: 2048px;
    height: 90%;
    align-items: center;
    justify-content: center;
    display: flex;
    padding: 50px;
    background-color: rgba(255, 255, 255, 1);
}

.divider {
    border-top: 3px solid rgba(0, 0, 0, 0.2);
    width: 100%;
}

.icon {
    width: 100%;
    height: 100%;
    background-position: 50% 50%;
    background-size: 100% 100%;
}

.icon-button {
    border-radius: 50% 50%;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.icon-button:hover {
    background-color: rgba(0, 0, 0, 0.05);
}

.github-icon {
    border-radius: 50% 50%;
    background-image: url("assets/image/github-mark-white.png");
    background-color: rgba(0, 0, 0, 1);
}

.instagram-icon {
    border-radius: 50% 50%;
    background-color: rgba(0, 0, 0, 1);
    background-image: url("assets/image/insta.png");
}

.portfolio-icon {
    border-radius: 50% 50%;
    /*background-color: rgba(0, 0, 0, 1);*/
    background-image: url("assets/image/default-profile-image.png");
}